<template>
  <div id="footerLabel">
    <a
      :href="l"
      target="_blank"
    >{{k}}</a>
      <span>{{info}}</span>
  </div>
</template>
<script>
export default {
  name: "footerLabel",
  props: {
    l: String,
    k: String,
    info: String,
  },
};
</script>
<style lang="scss">
@import "../assets/scss/music.scss";
#footerLabel {
  width: 248px;
  height: 80px;
  position: relative;
  a {
    width: 110px;
    @include fontHidden();
    display: inline-block;
    height: 30px;
    line-height: 30px;
    padding: 0px 12px;
    border-radius: 12px;
    border: 2px solid $colorA;
    position: absolute;
    left: 0px;
    top: 44px;
    background: $colorA;
    color: $colorD;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    z-index: 2;
    border-bottom-right-radius: 40px;
    border-top-left-radius: 40px;
    &:hover {
      + span {
        transform: translateX(0px);
        opacity: 1;
      }
    }
  }
  span {
    display: inline-block;
    width: auto;
    padding: 0 20px;
    height: 30px;
    line-height: 30px;
    background: $colorA;
    border-radius: 30px;
    text-align: center;
    color: $colorD;
    position: absolute;
    top: 2px;
    opacity: 0;
    transition: all 1s;
    transform: translateX(80px);
    z-index: 10;
    border-bottom-right-radius: 40px;
    border-top-left-radius: 40px;
  }
}
</style>