<template>
  <div>
    <nav-header :flag="isSticky"></nav-header>
    <router-view></router-view>
    <nav-footer v-if="!isloading"></nav-footer>
  </div>
</template>
<script>
import navHeader from "../components/NavHeader";
import navFooter from "../components/NavFooter";
export default {
  name: "Home",
  components: {
    navHeader,
    navFooter,
  },
  data() {
    return {
      isSticky: false,
      isloading: this.$store.state.loading,
    };
  },
  watch: {
    // 监听 vuex 中 loading 的变化， 使其能够动态赋值给 data 数据中
    "$store.state.loading": function() {
      this.isloading = this.$store.state.loading;
    },
  },
  mounted() {
    window.addEventListener("scroll", this.menu);
  },
  methods: {
    menu() {
      let value = document.documentElement.scrollTop || document.body.scrollTop;
      this.isSticky = value > 75 ? true : false;
    },
  },
};
</script>
